import { forwardRef, useState, FC } from "react";
import {
    Button,
    Card,
    CloseButton,
    Container,
    Grid,
    MultiSelect,
    SelectItem,
    SimpleGrid,
    Text,
    Title,
    UnstyledButton
} from "@mantine/core";
import type {
    MantineSize,
    MantineNumberSize,
    MultiSelectValueProps
} from "@mantine/core"

import { Asset, Balance } from "~/moralis.server";

type Dict<T> = {
    [key: string]: T
};

type TokenSelectProps = {
    title: string
    data: Asset[] | Balance[]
    placeholder: string
};

type PayProps = {
    label: string,
    value: string
};

type PayValueProps = {
    value: string,
    label: string;
    onRemove(): void;
    disabled: boolean;
    size: MantineSize;
    radius: MantineNumberSize;
};

const PayerSelect = (props: any) => {
    const { title, balances, placeholder } = props;
    const balanceMap: Dict<Balance> = {};
    const items: SelectItem[] = balances.map((b: Balance) => {
        balanceMap[b.token_address] = b;
        return {
            label: `${b.name} (${b.symbol})`,
            value: b.token_address
        }
    });

    const [value, setValue] = useState<string[]>([])

    // console.log(value);

    const PayValue = forwardRef<HTMLDivElement, PayValueProps>(
        ({
            label,
            value,
            onRemove
        }: PayValueProps, ref) => {
            return (
                <div ref={ref} style={{ width: "100%" }}>
                    {/* <SimpleGrid cols={1}>
                        <Button mt="md"> */}
                    {/* <Card withBorder p="xl" radius="md" mt="lg"> */}
                        <Grid>
                            <Grid.Col span={9}>
                                <Text size="md">{balanceMap[value].name}</Text>
                            </Grid.Col>
                            <Grid.Col span={3}>
                                <CloseButton
                                    onMouseDown={onRemove}
                                    variant="transparent"
                                    size={22}
                                    iconSize={14}
                                    tabIndex={-1}
                                />
                            </Grid.Col>
                        </Grid>
                    {/* </Card> */}
                    {/* </Button>
                    </SimpleGrid> */}
                </div>
            );
        }
    );

    const PayItem = forwardRef<HTMLDivElement, PayProps>(
        ({ label, value, ...others }: PayProps, ref) => {
            return (
                <div ref={ref} style={{ width: "100%" }} {...others}>
                    {/* <SimpleGrid cols={1}>
                        <Button variant="outline" m="xs"> */}
                    <Text size="md">{balanceMap[value].name}</Text>
                    {/* </Button>
                    </SimpleGrid> */}
                </div>
            );
        }
    );

    return (
        <Card withBorder p="xl" radius="md" mt="lg">
            <Title order={4}>{title}</Title>
            {/* {value.map((v: string, i: number) => {
                return (
                    <SimpleGrid cols={1}>
                        <Button mt="md" key={i}>
                            <Text size="md">{balanceMap[v].name}</Text>
                        </Button>
                    </SimpleGrid>
                )
            })} */}
            <MultiSelect
                data={items}
                itemComponent={PayItem}
                valueComponent={PayValue}
                value={value}
                onChange={setValue}
                mt="md"
                size="md"
                placeholder={placeholder}
                searchable
                nothingFound="Nothing found"
            // clearable
            // clearButtonLabel="Clear selected tokens"
            // filter={(value, selected, item) =>
            //     !selected &&
            //     (item.label?.toLowerCase().includes(value.toLowerCase().trim()) ||
            //         item.description.toLowerCase().includes(value.toLowerCase().trim()))
            // }
            />
        </Card>
    );
};

type ReceiveProps = {
    value: string
};

const ReceiveItem = forwardRef<HTMLDivElement, PayProps>(
    (props: ReceiveProps, ref) => {
        const { value } = props;
        return (
            <div ref={ref}>
                <Text size="md" mt="md">{value}</Text>
            </div>
        );
    }
);

const ReceiverSelect = (props: TokenSelectProps) => {
    const { title, data, placeholder } = props;
    const names = data.map(d => d.name);

    const [value, setValue] = useState<string[]>([])

    return (
        <Card withBorder p="xl" radius="md" mt="lg">
            <Title order={4}>{title}</Title>
            {/* {value.map((v: string, i: number) => {
                return (
                    <ReceiveItem value={v} key={i}/>
                )
            })} */}
            <MultiSelect
                data={names}
                value={value}
                onChange={setValue}
                mt="md"
                size="md"
                placeholder={placeholder}
                searchable
                nothingFound="Nothing found"
                clearable
                clearButtonLabel="Clear selected tokens"
            />
        </Card>
    );
};

type SwapProps = {
    assets: Asset[]
    balances: Balance[]
};

const Swap = (props: SwapProps) => {
    const { assets, balances } = props;

    return (
        <Container>
            <PayerSelect
                title="Pay Tokens"
                balances={balances}
                placeholder="Select tokens to deposit"
            />
            <ReceiverSelect
                title="Receive Tokens"
                data={assets}
                placeholder="Select tokens to withdraw"
            />
        </Container>
    );
};

export default Swap;
